@import url(https://fonts.googleapis.com/css2?family=Blinker:wght@700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap);
.fractal__blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    cursor: pointer;
}

.fractal__blog-container_article:hover {
    background: var(--color-hover);
}

.fractal__blog-container_article:active {
    background: var(--color-active);
}

.fractal__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.fractal__blog-container_article-image img {
    width: 100%;
    height: 100%;
}

.fractal__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.fractal__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;

    color: #fff;
}

.fractal__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    
    color: #fff;
    margin-bottom: 5rem;
    
}

.fractal__blog-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .fractal__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}
.fractal__blog {
    display: flex;
    flex-direction: column;    
}

.fractal__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.fractal__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.fractal__blog-container {
    display: flex;
    flex-direction: row;
}

.fractal__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.fractal__blog-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .fractal__blog-container {
        flex-direction: column-reverse;
    }


    .fractal__blog-container_groupA {
        margin: 2rem 0;
    }

    .fractal__blog-container_groupA .fractal__blog-container_article  {
       width: 48%;
    }

    .fractal__blog-container_groupA .fractal__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .fractal__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .fractal__blog-container_groupA .fractal__blog-container_article  {
       width: 100%;
    }

    .fractal__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .fractal__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.fractal__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.fractal__features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.fractal__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.fractal__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px #00f7ff40;
    margin-bottom: 0.25rem;
}

.fractal__features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.fractal__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .fractal__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .fractal__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .fractal__features-container__feature {
        margin: 1rem 0;
    }
}
.fractal__features {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));
}

.fractal__features-feature {
    display: flex;
}


.fractal__features-feature .fractal__features-container__feature {
    margin: 0;
}


.fractal__features-feature .fractal__features-container_feature-text {
    max-width: 700px;
}

.fractal__features-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
    margin: 4rem 0 2rem;
}

.fractal__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
}

.fractal__features-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.fractal__features-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.fractal__features-container .fractal__features-container__feature {
    flex: 1 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.fractal__features-container .fractal__features-container_feature-text {
    margin-top: 0.5rem;
}


@media screen and (max-width: 850px) {
    .fractal__features-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .fractal__features-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    .fractal__features-feature .fractal__features-container__feature {
        flex-direction: column;
    }

    .fractal__features-feature .fractal__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .fractal__features-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    
    .fractal__features-container .fractal__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
.fractal__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.fractal__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
}

.fractal__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.fractal__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
}

.fractal__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.fractal__footer-links_logo img {
    width: 118px;
    height: 30px;

    margin-bottom: 1rem;
}

.fractal__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

.fractal__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.fractal__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.fractal__footer-links_div a {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.fractal__footer-links_div a:hover {
    color:var(--color-hover);
    
}

.fractal__footer-links_div a:active {
    color:var(--color-active2);
    
}


.fractal__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.fractal__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .fractal__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .fractal__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
        
    }

    .fractal__footer-links div {
        margin: 1rem;
        
    }

    .fractal__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .fractal__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
        display: flex;
        flex-direction: column;
    }
}

.fractal__header {
    display: flex;
    flex-direction: column;
    background-color: var(--color-bg);
}

.fractal__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--color-bg);

    margin-right: 5rem;
    margin-top: 0.5rem;
    margin-bottom: 1.7rem;
    
}

.fractal__header-content h1 {
    font-family: var(--font-h1);
    font-weight: 800;
    font-size: 70px;
    letter-spacing: -0.02em;
}

.fractal__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-sec);

    margin-top: 1.5rem;
}

.fractal__header-content__input {
    width: 50%;
    margin: 3rem 0 1rem;
    background-color: var(--color-sec);
 
    color: var(--color-text2);
    border: 0.2rem solid var(--color-sec);
    border-radius: 0.5rem;
    display: flex;
    flex-direction: row;
}

