.fractal__header-content__input button {
    padding: 0 3rem;
    flex: 1 1;
    width: 100%;
    min-height: 0rem;
    font-family: var(--font-family);
    font-weight: 1000;
    font-size: 1.2rem;
    line-height: 2rem;
    background: var(--color-text2);

    border: 0rem solid  var(--color-sec);
    border-radius: 0.3rem;
    color: var(--color-text);
    cursor: pointer;
    outline: none;
    }

    .fractal__header-content__input button:hover {
        background: var(--color-bg);
        color: var( --color-hoverDark);
        transition: background-color .6s ease,color .2s ease;
    }

    .fractal__header-content__input button:active {
        background: var(--color-active);
        color: var(--color-text);
    }

    .fractal__header-content__button:-ms-input-placeholder {
        color: var(--color-text);
        font-weight: 800;
        
    }

    .fractal__header-content__button::placeholder {
        color: var(--color-text);
        font-weight: 800;
        
    }

.fractal__header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* .fractal__header-image img {
    width: 100%;
    height: 100%;
} */

@media screen and (max-width: 1050px) {
    .fractal__header {
        flex-direction: column;
    }

    .fractal__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .fractal__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .fractal__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .fractal__header-content__people {
        flex-direction: column;
    }

    .fractal__header-content__people p {
        margin: 0;
    }

    .fractal__header-content__input input,
    .fractal__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .fractal__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .fractal__header-content p {
        font-size: 14px;
        line-height: 24px;
    }
}
@media screen and (max-width: 490px) {
    .fractal__header {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: auto;
    }

    .fractal__header-content p {
        font-size: 14px;
        line-height: 24px;
    }
}
.fractal__explorer {
    
    display:flex;
    justify-content: space-between;
    flex-direction: row;

    

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));

}

.fractal__explorer-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.fractal__explorer-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.fractal__explorer-heading p {
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.fractal__explorer-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 1200px) {
    .fractal__explorer {
        flex-direction: column;
    }

    .fractal__explorer-heading {
        margin: 2 2 2rem 0;
    }
}

@media screen and (max-width: 990px) {
    .fractal__explorer {
        flex-direction: column;
    }

    .fractal__explorer-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .fractal__explorer-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}
.fractal__about {
    display: flex;
    flex-direction: row;
}

.fractal__about-image {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.fractal__about-image img {
    width: 100%;
    height: 100%;
}

.fractal__about-content {
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;
}

.fractal__about-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.fractal__about-content h4:last-child {
    color: #ff8A71;
}

.fractal__about-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.fractal__about-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;

    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .fractal__about {
        flex-direction: column;
    }

    .fractal__about-image {
        margin: 1rem 0;
    }

    .fractal__about-image img {
        width: unset;
        height: unset;
    }

    .fractal__about-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .fractal__about-image img {
        width: 100%;
        height: 100%;
    }
}


.fractal__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.fractal__brand div {
    flex: 1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
}
.fractal__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.fractal__cta-content {
    display: flex;
    flex-direction: column;

    text-align: left;
    color: #fff;
}

.fractal__cta-content p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
}

.fractal__cta-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
}

.fractal__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 2rem;
}


.fractal__cta-btn button:hover{
        background: var(--color-hover);
}
    
.fractal__cta-btn button:active{
        background: var(--color-active);
        filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));
}

.fractal__cta-btn button {
    background: var(--gradient-reverse-text);
    color: #fff;
    border-color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 2rem;

    

    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .fractal__cta {
        flex-direction: column;
    }

    .fractal__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .fractal__cta {
        flex-direction: column;
        margin: 4rem 2rem;
    }

    .fractal__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .fractal__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}

.fractal__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
}

.fractal__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.fractal__navbar-links_logo {
    margin-right: 2rem;
}

.fractal__navbar-links_logo img {
    width: 12rem;
    height: 4rem;
}

.fractal__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.fractal__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.fractal__navbar-links_container p,
.fractal__navbar-sign p,
.fractal__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}
.fractal__navbar-links_container p:hover{
    color: var(--color-hoverLinks);
}

.fractal__navbar-links_container p:active{
    color: var(--color-active2);
}

.fractal__navbar-sign p:hover{
    color: var(--color-hoverLinks);
}

.fractal__navbar-sign p:active{
    color: var(--color-active2);

}

.fractal__navbar-sign button:hover {
    background: var(--color-button);
    color: var(--color-text);

}

.fractal__navbar-sign button:active { 
    background: var(--color-text);
}


.fractal__navbar-sign button,
.fractal__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: var(--color-text2);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: 2px solid var(--color-hover);
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.fractal__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.fractal__navbar-menu svg {
    cursor: pointer;
}

.fractal__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.fractal__navbar-menu_container p {
    margin: 1rem 0;
}

.fractal__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .fractal__navbar-links_container {
        display: none;
    }

    .fractal__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .fractal__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .fractal__navbar {
        padding: 2rem;
    }

    .fractal__navbar-sign {
        display: none;
    }

    .fractal__navbar-menu_container {
        top: 20px;
    }

    .fractal__navbar-menu_container-links-sign {
        display: block;
    }
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg {

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 3% 25%, rgb(2, 1, 2) 0%, rgb(15, 14, 15) 25%);
}

.gradient__text {
  background: var(--color-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 2rem 3rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.App{
  margin: 0;
}

.scale-up-center {
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }  
}
