.fractal__features {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));
}

.fractal__features-feature {
    display: flex;
}


.fractal__features-feature .fractal__features-container__feature {
    margin: 0;
}


.fractal__features-feature .fractal__features-container_feature-text {
    max-width: 700px;
}

.fractal__features-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
    margin: 4rem 0 2rem;
}

.fractal__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
}

.fractal__features-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.fractal__features-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.fractal__features-container .fractal__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.fractal__features-container .fractal__features-container_feature-text {
    margin-top: 0.5rem;
}


@media screen and (max-width: 850px) {
    .fractal__features-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .fractal__features-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    .fractal__features-feature .fractal__features-container__feature {
        flex-direction: column;
    }

    .fractal__features-feature .fractal__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .fractal__features-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    
    .fractal__features-container .fractal__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}