.fractal__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.fractal__cta-content {
    display: flex;
    flex-direction: column;

    text-align: left;
    color: #fff;
}

.fractal__cta-content p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
}

.fractal__cta-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
}

.fractal__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 2rem;
}


.fractal__cta-btn button:hover{
        background: var(--color-hover);
}
    
.fractal__cta-btn button:active{
        background: var(--color-active);
        filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));
}

.fractal__cta-btn button {
    background: var(--gradient-reverse-text);
    color: #fff;
    border-color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 2rem;

    

    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .fractal__cta {
        flex-direction: column;
    }

    .fractal__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .fractal__cta {
        flex-direction: column;
        margin: 4rem 2rem;
    }

    .fractal__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .fractal__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}
