.fractal__explorer {
    
    display:flex;
    justify-content: space-between;
    flex-direction: row;

    

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #36017c 25%, #320b66 85%, #06e0e0 120%);

    filter: drop-shadow(0.2rem 0.2rem 0.2rem var(--color-footer));

}

.fractal__explorer-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.fractal__explorer-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.fractal__explorer-heading p {
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.fractal__explorer-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 1200px) {
    .fractal__explorer {
        flex-direction: column;
    }

    .fractal__explorer-heading {
        margin: 2 2 2rem 0;
    }
}

@media screen and (max-width: 990px) {
    .fractal__explorer {
        flex-direction: column;
    }

    .fractal__explorer-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .fractal__explorer-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}