.fractal__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.fractal__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
}

.fractal__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.fractal__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
}

.fractal__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.fractal__footer-links_logo img {
    width: 118px;
    height: 30px;

    margin-bottom: 1rem;
}

.fractal__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

.fractal__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.fractal__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.fractal__footer-links_div a {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.fractal__footer-links_div a:hover {
    color:var(--color-hover);
    
}

.fractal__footer-links_div a:active {
    color:var(--color-active2);
    
}


.fractal__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.fractal__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .fractal__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .fractal__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
        
    }

    .fractal__footer-links div {
        margin: 1rem;
        
    }

    .fractal__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .fractal__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
        display: flex;
        flex-direction: column;
    }
}
