.fractal__header {
    display: flex;
    flex-direction: column;
    background-color: var(--color-bg);
}

.fractal__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--color-bg);

    margin-right: 5rem;
    margin-top: 0.5rem;
    margin-bottom: 1.7rem;
    
}

.fractal__header-content h1 {
    font-family: var(--font-h1);
    font-weight: 800;
    font-size: 70px;
    letter-spacing: -0.02em;
}

.fractal__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-sec);

    margin-top: 1.5rem;
}

.fractal__header-content__input {
    width: 50%;
    margin: 3rem 0 1rem;
    background-color: var(--color-sec);
 
    color: var(--color-text2);
    border: 0.2rem solid var(--color-sec);
    border-radius: 0.5rem;
    display: flex;
    flex-direction: row;
}

